import React, { useEffect, useState } from "react";
import mode1 from'../../ASSETS/Images/Banner/modejeu1.png'
import mode2 from '../../ASSETS/Images/Banner/modejeu2.png'

import './BannerModeJeuStyles.scss'
const BannerModeJeu = () => {
    useEffect(() => {
        const elementsToAnimate = document.querySelectorAll(".cardimage.animated, .cardModetilte.animated, .textcardmode.animated, .listescardmode.animated");

        const observer = new IntersectionObserver(
            (entries) => {
                entries.forEach((entry) => {
                    if (entry.isIntersecting) {
                        entry.target.classList.remove("hidden");
                        observer.unobserve(entry.target);
                    }
                });
            },
            { threshold: 0.9 }
        );

        elementsToAnimate.forEach((element) => {
            observer.observe(element);
        });

        return () => {
            elementsToAnimate.forEach((element) => {
                observer.unobserve(element);
            });
        };
    }, []);
    const [showMoreMode1, setShowMoreMode1] = useState(false);
    const [showMoreMode2, setShowMoreMode2] = useState(false);

    // Fonctions pour basculer l'affichage des lments supplmentaires
    const toggleShowMoreMode1 = () => setShowMoreMode1(!showMoreMode1);
    const toggleShowMoreMode2 = () => setShowMoreMode2(!showMoreMode2);
    return(
        <div className='containerModeJeu'>
            <h1 className='titleMode'>Modes de jeu :</h1>
            <h2 className='titleH2Mode'>Notre jeu proposera  terme plusieurs variantes pour satisfaire tous les types de joueurs. Que vous prfriez les combats en arne, les dfis en coopration ou les campagnes solo, vous trouverez un mode de jeu qui vous convient. Chaque mode offre des rcompenses uniques et des expriences de jeu passionnantes.</h2>
            <div className='contenuModeJeu'>
                <div className='mode'>
                    <h2 className='cardModetilte animated  hidden'>Arkanys Arena</h2>
                    <img className='cardimage' src={mode1} alt='image mode jeu 1'/>
                    <p className='textcardmode animated  hidden'>Le mode "Arkanys Arena" d'Arkanys offre une exprience de jeu immersive et stratgique pour satisfaire les joueurs  la recherche de dfis comptitifs. Ce mode met l'accent sur la personnalisation des decks, la gestion des ressources et la progression  travers diffrentes arnes, offrant ainsi une exprience de jeu riche et captivante.
                        Dans le mode "Arkanys Arena", les joueurs construisent des decks composs de hros, de monstres, de sorts et de tours de dfense. Ils peuvent amliorer leurs monstres, sorts et tours de dfense en fusionnant des cartes de mme niveau et raret. Les hros, quant  eux, sont amliors en dpensant des gemmes d'exprience, obtenues en augmentant le niveau du compte joueur. Les quipements renforcent les statistiques des hros et ncessitent un certain niveau d'exprience pour tre quips.
                        Gameplay :
                    </p>
                    {/* Ajoutez cette condition pour afficher les lments supplmentaires */}
                    {showMoreMode1 && (
                        <>
                            <p className='textcardmode' style={{display: showMoreMode1 ? 'block' : 'none'}}>Le gameplay du mode "Arkanys Arena" est bas sur des combats en tour par tour entre deux joueurs, chacun disposant de 150 secondes pour jouer au cours d'une partie et de 15 secondes par tour de jeu. Les combats ont lieu dans diffrentes arnes, chacune offrant des rcompenses et des dfis croissants. Le matchmaking est conu pour associer les joueurs ayant un niveau de comptence similaire, garantissant ainsi des combats quilibrs et stimulants.</p>
                            <p className='textcardmode'style={{display: showMoreMode1 ? 'block' : 'none'}}>Au cours des combats, les joueurs dploient leurs units et lancent des sorts en utilisant de l'ether, une ressource qui se rgnre automatiquement tout au long de la partie. Chaque unit et sort a un cot en ether, obligeant les joueurs  prendre des dcisions stratgiques sur la manire de grer leurs ressources pour dployer efficacement leurs troupes et remporter le combat.</p>
                            <p className='textcardmode' style={{display: showMoreMode1 ? 'block' : 'none'}}>Si aucun joueur n'a atteint l'un des deux objectifs (liminer tous les personnages adverses ou dtruire les tours) dans les 5 minutes imparties, une prolongation est dclenche. Durant cette prolongation, chaque joueur reoit 60 secondes supplmentaires et l'octroi d'ether est augment par tour, ce qui intensifie l'action et encourage les joueurs  laborer des stratgies plus agressives pour remporter la victoire.</p>
                            <p className='textcardmode' style={{display: showMoreMode1 ? 'block' : 'none'}}>Les victoires rapportent des trophes pour progresser dans les classements et accder  des rcompenses plus importantes. De plus, les victoires permettent aux joueurs de gagner des coffres contenant des cartes de hros, de monstres, d'quipements et de sorts pour amliorer et personnaliser leurs decks.</p>
                            <p className='textcardmode' style={{display: showMoreMode1 ? 'block' : 'none'}}>Le mode "Arkanys Arena" d'Arkanys offre une exprience de jeu passionnante et stimulante, attirant  la fois les investisseurs et les joueurs grce  son gameplay stratgique, sa personnalisation approfondie et son systme de progression attrayant.</p>

                        </>
                    )}
                    <button onClick={toggleShowMoreMode1}  className="showMoreButton">
                        {showMoreMode1 ? "Voir moins" : "Voir plus"}
                    </button>
                </div>

                <div className='mode' id='modeDeJeux'>
                    <h2 className='cardModetilte animated  hidden'>Le Monde de Tokenia: Les Gardiens d'Arkanys</h2>
                    <img className='cardimage ' src={mode2} alt='image mode jeu 2'/>
                    <p className='textcardmode animated hidden'>Les Gardiens d'Arkanys (LGA) est le mode de jeu final d'Arkanys.
                        Dans ce mode, les joueurs explorent un vaste monde peupl de monstres, de qutes et de dfis. Les hros, les monstres et les quipements amliors dans les autres modes de jeu sont essentiels pour progresser dans LGA, o les joueurs devront affronter des ennemis de plus en plus puissants et rsoudre des nigmes complexes pour avancer dans l'histoire.
                        Gameplay :
                        LGA est un jeu de rle et d'exploration o les joueurs contrlent leur hros et son quipe de monstres dans un monde ouvert et interactif. Les combats dans Evolve sont bass sur un systme de combat au tour par tour, o les joueurs doivent utiliser la stratgie et la rflexion pour vaincre leurs ennemis.
                        Les hros, les monstres et les quipements obtenus et amliors dans les autres modes de jeu sont essentiels pour progresser dans LGA.
                        Les joueurs devront galement rsoudre des nigmes, accomplir des qutes et interagir avec des personnages non-joueurs pour avancer dans l'histoire et dbloquer de nouvelles zones  explorer.
                        Les rcompenses gagnes dans LGA, telles que des cartes de hros, des monstres, des quipements et des ressources, peuvent tre utilises pour amliorer et personnaliser les decks dans tous les modes de jeu, renforant ainsi l'interconnexion entre les modes et incitant les joueurs  dcouvrir et profiter de toutes les facettes d'Arkanys.</p>
                    {showMoreMode2 && (
                        <>

                            <ul>
                                <li className= 'listescardmode'>Exploration et qutes: Les joueurs exploreront un vaste monde ouvert rempli de divers environnements, points d'intrt, et monstres  affronter. Ils pourront accomplir des qutes pour progresser dans l'histoire et dbloquer de nouvelles zones.</li>
                                <li className= 'listescardmode'>PvM - Combat contre les monstres: Les joueurs pourront former des groupes pour affronter des monstres dans des combats tactiques au tour par tour. Chaque monstre possdera des caractristiques et des comptences uniques, obligeant les joueurs  laborer des stratgies pour les vaincre. Les combats PvM offriront des rcompenses sous forme d'exprience, d'objets et de ressources.</li>
                                <li className= 'listescardmode'>PvP - Combat entre joueurs: Les joueurs pourront galement s'affronter dans des combats PvP au tour par tour. Ces combats pourront tre organiss dans des arnes ddies, permettant aux joueurs de montrer leurs comptences et de se mesurer  d'autres joueurs pour gagner des rcompenses et amliorer leur classement.</li>
                                <li className= 'listescardmode'>Personnalisation et progression: Les joueurs pourront personnaliser et amliorer leurs hros en acqurant de nouveaux quipements, comptences, et familiers. Ils gagneront de l'exprience en combattant des monstres et d'autres joueurs, ce qui leur permettra de monter en niveau et de dbloquer de nouvelles capacits.</li>
                                <li className= 'listescardmode'>Coopration et interaction sociale: Les joueurs pourront rejoindre des guildes et cooprer avec d'autres joueurs pour relever des dfis PvM et PvP plus difficiles. La communication et la coordination avec les coquipiers seront essentielles pour russir dans les combats les plus difficiles.</li>
                                <li className= 'listescardmode'>Rcompenses et conomie du jeu: Les joueurs gagneront des rcompenses en accomplissant des qutes, en combattant des monstres et en participant  des combats PvP. Ils pourront changer ces rcompenses sur le march pour obtenir des objets rares et amliorer leurs hros.</li>

                            </ul>

                            <p className='textcardmode'>Ce mode de jeu inspir des meilleurs MMORPG offrira une exprience de jeu riche et immersive, permettant aux joueurs d'explorer un monde fantastique, de combattre des monstres et d'autres joueurs, et de progresser dans un systme de combat tactique au tour par tour.</p>
                        </>
                    )}
                        <button onClick={toggleShowMoreMode2}  className="showMoreButton">
                        {showMoreMode2 ? "Voir moins" : "Voir plus"}
                    </button>
                </div>
            </div>

        </div>
    )
}

export default BannerModeJeu